export class PostsManageBean {
    productId: string = '';  //可售产品id
    productName: string;  //产品名称
    productTemplateId: string; //基础产品id  
    productTypeId: string = 'AvailableProduct';//（默认值）可售产品类型id
    operationTemplateId: string = ' ';//基础产品id
    description: string = ''; //可售描述
    introductionDate: any;//引入日期（格式yyyy-MM-dd HH:mm:ss）
    releaseDate: any;//截止日期，（格式yyyy-MM-dd）
    productFeature: any = [];//属性值
    productMsg: [//产品通用信息、管理信息、特殊属性
        {
            attrName: string;
            attrValue: string;
            attrType: string;
        }
    ]
    productMarketingList: [//产品营销
        {
            productMarketingKey: string;
            productMarketingValue: string;
            productMarketingType: string;
        }
    ]
    productTemplateName: string; //基础产品名称
    productLineName: string; //产品线名称
    productLineId: string; //产品线id
    productGroupId: string; //产品组id
    pageSize: number;//每页显示数量
    pageNum: number; //当前页
    operationProductId: string; //在可售修改中为基础产品id
    functionList: any;//可售功能
    productContent: any = [
        {
            channelId: '',//站点ID
            imageList: [],//图片集合
            fileList: [],//文档集合
            infoList: [],//描述集合
        }
    ]
}

//产品编号
export class productNumBean {
    productNum: { productCategoryId: '01', categoryName: '000' };
}
//产品查询
export class ProductQBean {
    productId: string; //			可售产品id	String	Y
    productName: string; //			产品名称	String	Y
    productTemplateId: string; //			基础产品id	String	Y
    productTemplateName: string; //			基础产品名称	String	Y
    productLineName: string; //			产品线名称	String	Y
    productLineId: string; //			产品线id	String	Y
    productGroupId: string; //			产品组id	String	Y
    productTypeId: string=''; //			可售产品类型id	String	Y
    pageSize: number=10; //			每页显示数量	Integer	Y
    pageNum: number=1; //			当前页	Integer	Y
}

