import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';


@Injectable()
export class SaleService {
    constructor() { }
    //字段排序，数据结构处理
    productFeatureManage(dataList) {
        let  isUpdate;
        dataList.forEach(parentItem => {
            parentItem.productFeatureTypeList.forEach(childItem => {
                let sequenceNum = childItem['sequenceNum'];
                if(typeof sequenceNum == 'undefined'){
                    childItem['sequenceNum'] = '';
                }
            })
        })
        return dataList;
    }

}
