import { ActivatedRoute, ParamMap } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { Message, TreeNode } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkNumber } from 'app/@uisftech/common/service/validator';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'credit-sale-product-modify-specail',
    templateUrl: './sale-product-modify-specail.component.html',
    styleUrls: ['./sale-product-modify-specail.component.scss'],
    providers: [CreditCcmService, Commfunc]
})

export class SaleProductModifySpecailComponent implements OnInit {
    @Input() inValue: any = [];
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    msgs: Message[] = [];
    temporary: any;//属性类型
    code: any;
    attrN: any = [];
    attrName: string = '';
    attrValue: string = '';
    attrType: string = '';
    loadd: boolean = false;
    arrValue: any = [];

    constructor(public creditCcmService: CreditCcmService, public commfunc: Commfunc, public route: ActivatedRoute) {
        this.codeValues() //调用方法，获取全部码值
        this.temporary = this.code['temporary'];
    }
    ngOnInit() {

    }
    ngOnChanges() {

    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    attrAdd() {
        this.inValue.forEach(item => {
            this.attrN.push(item.attrName)
        });
        if (this.attrName == '' || this.attrType == '' || this.attrValue == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '特殊属性填写不完整！' });
        } else {
            if (this.attrN.indexOf(this.attrName) == -1) {
                let lod1 = checkNumber(null, this.attrValue)
                if (lod1 != null) {
                    this.loadd = lod1.checkNumber;
                }
                this.arrValue = this.attrValue.split('');
                if (this.attrType == '0' && this.loadd == true) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '属性取值为数据！' });
                    this.loadd = false;
                } else if (this.attrType == '0' && this.arrValue.length > 20) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '数据长度不可超过20！' });
                } else if (this.attrType == '2' && this.arrValue.length > 200) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '数文本长度不可超过200！' });
                } else {
                    let paramss = {}
                    this.inValue.push({
                        "attrName": this.attrName,
                        "attrType": this.attrType,
                        "attrValue": this.attrValue
                    });
                    paramss = {
                        displaed: false,
                        salet2: this.inValue,
                    }
                    this.outValue.emit(paramss);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '属性名称重名' });
            }
        }
        this.attrN = [];
        this.attrName = '';
        this.attrValue = '';
        this.attrType = '';
    }
    attrcanel() {
        let paramss = {
            displaed: false,
            salet2: this.inValue,
        }
        this.outValue.emit(paramss);
    }
}