import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//删除

import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { SaleProductComponentBean } from 'app/pages/tzb/custom/credit-ccm/credit-product-config/bean/sale-product-component.bean';

@Component({
    selector: 'app-sale-product-component-select',
    templateUrl: './sale-product-component.component.html',
    styleUrls: ['./sale-product-component.component.scss'],
    providers: [CreditCcmService, ConfirmationService, Commfunc]
})

export class SaleProductComponentSelect implements OnInit {

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();

    @Output() closeDialog: EventEmitter<any> = new EventEmitter();

    tabMesg: any = tableMessage;//列表无数据
    // bean
    componentViewBean: SaleProductComponentBean = new SaleProductComponentBean();

    selectComponent: any;

    select_1: SelectItem[] = [];//默认下拉
    select_line: any = [];//产品线下拉
    select_group: any = [];//产品组下拉
    line_value: string;//产品线下拉值
    group_value: string;//产品组下拉值

    //头部标题
    all_title: string = '组件选择';
    //列表
    cars_2: any;
    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';
    //模态框
    showModel: string;
    headerTitle: string
    display: boolean = false;
    //提示信息
    msgs: Message[] = [];
    //分页控制
    first: number = 0;
    constructor(
        public creditCcmService: CreditCcmService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc) {
        this.select_1 = [
            { label: '请选择', value: " " },
        ];
        this.select_line = [
            { label: '请选择', value: " " },
        ];
        this.select_group = [
            { label: '请选择', value: " " },
        ];

    }

    ngOnInit() {
        this.select_line = [
            { label: '请选择', value: " " },
        ];
        this.select_group = [
            { label: '请选择', value: " " },
        ];
        this.componentViewBean.pageSize = 10;
        this.componentViewBean.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }

    // 重置
    resetClick() {
        this.componentViewBean = new SaleProductComponentBean();
    }

    //查询始终第一页
    querySecle() {
        this.componentViewBean.pageSize = 10;
        this.componentViewBean.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }

    //查询
    queryClick() {
        this.componentViewBean.productCategoryLineId = this.line_value;
        this.componentViewBean.productCategoryGroupId = this.group_value;
        this.creditCcmService.productFeatureCategoryList(this.componentViewBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.cars_2 = data.productFeatureCategoryList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.componentViewBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.componentViewBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.componentViewBean.pageSize;
        //调用查询的方法
        this.queryClick();
        this.componentViewBean.pageNum = 1;
    }

    // 确定选择
    confirmChoose() {
        if (this.selectComponent) {
            // 判断是否重复
            if (this.inValue) {
                let hasRepeat = false;
                this.inValue.forEach(item => {
                    if (!hasRepeat && item === this.selectComponent.productFeatureCategoryId) {
                        hasRepeat = true;
                    }
                });
                if (hasRepeat) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'info', summary: '提示', detail: '该组件已被引入过！' });
                    return;
                }
            }
            // 不重复则需要查询组件属性信息并返回
            this.creditCcmService.findProductFeatureByCategoryIdCredit({ "productFeatureCategoryId": this.selectComponent.productFeatureCategoryId }).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.productFeatureTypeList === '' || data.productFeatureTypeList === undefined) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'info', summary: '提示', detail: '该组件下没有属性！' });
                        return;
                    }
                    const productFeatureCategoryName = data.description;
                    data.productFeatureTypeList.forEach(item => {
                        item['productFeatureCategoryName'] = productFeatureCategoryName;
                    });
                    this.outValue.emit(data);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'info', summary: '提示', detail: '请选择一个组件！' });
        }
    }

    test() {
        if (this.inValue) {
            let hasRepeat = false;
            this.inValue.forEach(item => {
                if (!hasRepeat && item === "PC00000001") {
                    hasRepeat = true;
                }
            });
            if (hasRepeat) {
                this.msgs = [];
                this.msgs.push({ severity: 'info', summary: '提示', detail: '该组件已被引入过！' });
                return;
            }
        }
        const test = {
            "productFeatureCategoryList": [
                {
                    "productFeatureCategoryType": "REQUIRED",
                    "productFeatureCategoryId": "PC00000001",
                    "description": "产品基础组件",
                    "parentCategoryId": "产品基础组件",
                    "productFeatureTypeList": [
                        {
                            "productFeatureTypeId": "PCA0000002",
                            "productFeatureTypeLocalCode": "无效状态不允许销售",
                            "productFeatureList": [
                                {
                                    "productFeatureTypeId": "PCA0000002",
                                    "min": "",
                                    "def": "",
                                    "max": "",
                                    "valueType": "01",
                                    "productFeatureId": "F000000002",
                                    "productFeatureCategoryId": "PC00000001",
                                    "description": "有效",
                                    "productFeatureLocalCode": "1",
                                    "inc": "null"
                                }
                            ],
                            "featureType": "01",
                            "description": "产品状态",
                            "configLevel": "0"
                        }
                    ]
                }
            ]
        }
        this.outValue.emit(test);
    }

    // 关闭弹框
    closeComponentSelect() {
        this.closeDialog.emit();
    }
}