import { ActivatedRoute, ParamMap } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { Message, TreeNode } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { GlobalDirectoryBean } from '../global-directory-model/bean/global-directory-model.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'credit-sale-product-config-copy',
    templateUrl: './sale-product-config-copy.component.html',
    styleUrls: ['./sale-product-config-copy.component.scss'],
    providers: [CreditCcmService, Commfunc]
})

export class SaleProductConfigCopyComponent implements OnInit {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    head_title: string = '全局产品目录';
    //提示信息
    msgs: Message[] = [];
    selectedFiles:TreeNode[];
    //列表
    cars_2: any;
    treeData: any;
    listData: any = {};

    //模态框
    showModel: string;
    headerTitle: string
    display: boolean = false;

    //组件的参数
    updateValue: any = [];
    globalDirectoryBean: GlobalDirectoryBean = new GlobalDirectoryBean();

    constructor(public creditCcmService: CreditCcmService, public commfunc: Commfunc, public route: ActivatedRoute) { }
    ngOnInit() {
        this.query_line();
    }
    ngOnChanges() {

    }

    query_line() {
        this.creditCcmService.productCategoryLineQueryList(this.globalDirectoryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                let tempArr = [];
                data.productLineList.forEach(item => {
                    let tempObj = { id: 'line' };
                    tempObj['label'] = item['productLineName'];
                    tempObj['value'] = item;
                    tempObj['children'] = [{}];
                    tempArr.push(tempObj)
                });
                this.treeData = tempArr;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    nodeCheck(event) {
        if (event.node.id == "keshou") {
            let param = {
                productTemplateId: event.node.value.operationTemplateId
            }
            let arr = [];
            this.creditCcmService.findProAvailableProductList(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.cars_2 = data.resultList;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
            let params = {
                productId: event.node.value.productId
            }
            this.creditCcmService.getAvailableProduct(params).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.listData = data;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        }
    }
    //+号点击
    nodeExpend(event) {
        if (event.node.id == "line") {
            let parmData1 = {
                pageNum: 1,
                pageSize: 10000,
                productLineId: event.node.value.productLineId,
            }
            let arr = [];
            this.creditCcmService.productCategoryGroupInfoQueryList(parmData1).subscribe(data => {
                if (data.returnCode.code == "success") {
                    data.productGroupInfoList.forEach(item => {
                        let tempObj = { id: 'group' };
                        tempObj['label'] = item['groupName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        arr.push(tempObj)
                    });
                    event.node.children = arr
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        }
        if (event.node.id == "group") {
            let param = {
                groupId: event.node.value.groupId
            }
            let arr = [];
            this.creditCcmService.pdGroupTemplateQuery(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    data.groupTempList.forEach(item => {
                        let tempObj = { id: 'jichu' };
                        tempObj['label'] = item['tplName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        arr.push(tempObj)
                    });
                    event.node.children = arr
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        }
        if (event.node.id == "jichu") {
            let param = {
                pageNum: 1,
                pageSize: 10000,
                productTemplateId: event.node.value.tplId
            }
            let arr = [];
            this.creditCcmService.findProAvailableProductList(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    data.resultList.forEach(item => {
                        let tempObj = { id: 'keshou' };
                        tempObj['label'] = item['productName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        arr.push(tempObj)
                    });
                    event.node.children = arr
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            })
        }
    }

    serve() {
        let parmes = {
            display: false,
            depart: this.listData,
        }
        this.outValue.emit(parmes);
    }
}