import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { SaleProductViewComponent } from './sale-product-view/sale-product-view.component';//可售产品查看
import { SaleProductConfigComponent } from './sale-product-config/sale-product-config.component';//可售产品新增
import { SaleViewDetailsComponent } from './sale-product-view/sale-view-details/sale-view-details.component';//可售产品详情
import { SaleProductModifyComponent } from './sale-product-modify/sale-product-modify.component';//可售产品修改

const routes: Routes = [
    {
        path: 'credit-product-view', component: SaleProductViewComponent,
    },
    {
        path: 'credit-product-details', component: SaleViewDetailsComponent,
    },
    {
        path: 'credit-product-config', component: SaleProductConfigComponent,
    },
    {
        path: 'credit-product-modify', component: SaleProductModifyComponent,
    },
   
];

export const CreditProductConfigRouting: ModuleWithProviders = RouterModule.forChild(routes);
