    //功能左移
    liftFunMovre() {
        let tempss = [];
        let tempss1 = [];
        let arrsleft = [];
        this.rightListFun.forEach(item => {
            arrsleft.push(item)
            arrsleft.sort(function (a, b) { return b - a; })
            this.funList_left.push(this.funList_right[item])
        });
        arrsleft.forEach((item, i) => {
            this.funList_right.splice(item, 1)
        });
        tempss = _.clone(this.funList_right)
        this.funList_right = _.clone(tempss)
        tempss1 = _.clone(this.funList_left)
        this.funList_left = _.clone(tempss1)
        this.rightListFun = []
    }
