import { ActivatedRoute, ParamMap } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { StepsModule, MenuItem, Message } from 'primeng/primeng';

import { QueryVPListBean } from '../been/sale-product-config.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'credit-sale-product-config-User',
    templateUrl: './sale-product-config-User.component.html',
    styleUrls: ['./sale-product-config-User.component.scss']
})

export class SaleProductConfigUserComponent implements OnInit {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    queryVPListBean: QueryVPListBean = new QueryVPListBean();
    param: any;
    lists_1: any = [];
    msgs: Message[] = [];
    choose_tr1: any = [];
    select_atr2: any = [];
    select_atr3: any = [];
    department: any = [];
    choose_traol: boolean = false;//组件全选;
    pageSize: number = 10;           //			每页显示数量	String(20)
    total: string;
    pageNum: number = 1;           //		当前页	String(20)
    //分页控制
    first: number = 0;
    constructor(public creditCcmService: CreditCcmService, public route: ActivatedRoute) { }
    ngOnInit() {
        this.first = 0;
    }

    ngOnChanges() {
        this.queryEvent();
    }

    queryEvent() {
        this.choose_tr1.forEach(item1 => {
            let choosenum = 0;
            this.department.forEach(items => {
                if (this.lists_1[item1].postId == items.postId) {
                    choosenum++;
                }
            });
            if (choosenum == 0) {
                this.department.push(this.lists_1[item1])
            }
        });
        this.choose_tr1.sort(function (a, b) { return b - a; })
        this.choose_tr1.forEach(item2 => {
            this.lists_1.splice(item2, 1);
        });
        this.lists_1.forEach(iteme => {
            let choosenu = 0;
            let choosen = 0;
            this.department.forEach((items, k) => {
                if (iteme.postId == items.postId) {
                    choosenu++;
                    choosen = k;
                }
            });
            if (choosenu > 0) {
                this.department.splice(choosen, 1)
            }
        });
        this.choose_tr1 = [];
        this.choose_traol = false;
        let param = {
            pageSize: this.pageSize,
            pageNum: this.pageNum,
            postId: this.queryVPListBean.postId,
            postName: this.queryVPListBean.postName,
        }
        this.creditCcmService.queryVaguePostList(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.lists_1 = data.postList;
                this.total = data.total;
                this.lists_1.forEach((itemse, i) => {
                    this.department.forEach(items => {
                        if (itemse.postId == items.postId) {
                            let nue = i.toString();
                            this.choose_tr1.push(nue);
                        }
                    });
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //查询
    queryclick() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryEvent();
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        //调用查询的方法
        this.queryEvent();
        this.pageNum = 1;
    }

    // 重置
    resetClick() {
        this.queryVPListBean = new QueryVPListBean()
    }

    server() {
        this.queryEvent();
        if (this.inValue == 'one') {
            let parmes = {
                value: this.inValue,
                display: false,
                depart: this.department,
            }
            this.outValue.emit(parmes);
        }
        if (this.inValue == 'two') {
            let parmes = {
                value: this.inValue,
                display: false,
                depart1: this.department,
            }
            this.outValue.emit(parmes);
        }
        if (this.inValue == 'tree') {
            let parmes = {
                value: this.inValue,
                display: false,
                depart2: this.department,
            }
            this.outValue.emit(parmes);
        }
    }

    close() {
        let parm = {
            display: false,
        }
        this.outValue.emit(parm);
    }

    //多选全选
    choose_traols() {
        this.select_atr2 = [];
        for (let i = 0; i < this.lists_1.length; i++) {
            if (this.choose_traol == true) {
                let k;
                k = i.toString();
                this.select_atr2.push(k);
                this.choose_tr1 = this.select_atr2;
            }
            else {
                this.choose_tr1 = [];
                this.select_atr2 = [];
            }
        }
    }

    //多选按钮
    choose_tros(ri) {
        this.select_atr3 = [];
        for (let i = 0; i < this.lists_1.length; i++) {
            let k;
            k = i.toString();
            this.select_atr3.push(k);
        }
        if (this.select_atr3.length === this.choose_tr1.length) {
            this.choose_traol = true;
        } else {
            this.choose_traol = false;
        }
    }
}